import { useState } from "react";
import { Link } from "react-router-dom"
import { BASE_URL } from "../App"

import { GoHome } from "react-icons/go"
import { BiListPlus } from "react-icons/bi"
import { AiOutlineSearch } from "react-icons/ai"
import { IoSettingsOutline } from "react-icons/io5"

const Tabs = ({ color }) => {
    const [openTab, setOpenTab] = useState(1);
    return (
        <div className="flex flex-wrap">
            <div className="w-full">
                <ul className="flex mb-0 list-none flex-wrap pt-3 pb-4 flex-row">
                    <li className="-mb-px mr-2 last:mr-0 flex-auto text-center">
                        <Link to={BASE_URL}
                            className={
                                "text-xs font-bold uppercase px-5 py-3 shadow-lg rounded block leading-normal " +
                                (openTab === 1 ? "text-white bg-" + color + "-600" : "text-" + color + "-600 bg-white")
                            }
                            onClick={() => setOpenTab(1)}
                        >
                            <i className="fas fa-space-shuttle text-base mr-1"></i> <GoHome size={32} />
                        </Link>
                    </li>
                    <li className="-mb-px mr-2 last:mr-0 flex-auto text-center">
                        <Link to={`${BASE_URL}content`}
                            className={
                                "text-xs font-bold uppercase px-5 py-3 shadow-lg rounded block leading-normal " +
                                (openTab === 2 ? "text-white bg-" + color + "-600" : "text-" + color + "-600 bg-white")
                            }
                            onClick={() => setOpenTab(2)}
                            >
                                <BiListPlus size={32} />
</Link>
                    </li>
                    <li className="-mb-px mr-2 last:mr-0 flex-auto text-center">
                        <Link to={`${BASE_URL}picture`}
                            className={
                                "text-xs font-bold uppercase px-5 py-3 shadow-lg rounded block leading-normal " +
                                (openTab === 3 ? "text-white bg-" + color + "-600" : "text-" + color + "-600 bg-white")
                            }
                            onClick={() => setOpenTab(3)}
                        >
                            <i className="fas fa-briefcase text-base mr-1"></i><AiOutlineSearch size={32} />
                        </Link>
                    </li>
                    <li className="-mb-px mr-2 last:mr-0 flex-auto text-center">
                        <Link to={`${BASE_URL}loading`}
                            className={
                                "text-xs font-bold uppercase px-5 py-3 shadow-lg rounded block leading-normal " +
                                (openTab === 4 ? "text-white bg-" + color + "-600" : "text-" + color + "-600 bg-white")
                            }
                            onClick={() => setOpenTab(4)}
                        >
                            <i className="fas fa-briefcase text-base mr-1"></i> <IoSettingsOutline size={32} />
                        </Link>
                    </li>
                </ul>
            </div>
        </div>
    );
};

export default function TabsRender() {
    return (
        <>
            <Tabs color="violet" />
        </>
    );
}
